export default function Index() {
    return (
        <Container maxWidth="sm">
            <Box my={4}>
                <Typography variant="h4" component="h1" gutterBottom>
                    Next.js example
                </Typography>
<SchoolCyberSafetyMap/>
            </Box>
        </Container>
    );
}


